import { createApp } from 'vue';
import ElementPlus from 'element-plus';
import 'element-plus/theme-chalk/index.css';
import App from './App.vue';
import router from './router/index.js';
import store from './store/store.js'; // Імпортуйте ваш Store тут

// Імпорт іконок
import * as ElementIcons from '@element-plus/icons-vue';
const savedLoggedInStatus = localStorage.getItem('isLoggedIn');

if (savedLoggedInStatus) {
  store.commit('SET_LOGGED_IN_STATUS', JSON.parse(savedLoggedInStatus));
}

const app = createApp(App);

// Додавання іконок до глобального об'єкта додатку
for (const icon in ElementIcons) {
  app.component(icon, ElementIcons[icon]);
}

app.use(ElementPlus);
app.use(router); // Використання роутера
app.use(store);  // Тут ми підключаємо Vuex Store до нашого додатку
app.mount('#app');
