<template>
  <el-form 
        :model="login" 
      >
        <h2 class="atification_title">Авторизація</h2>
        <el-form-item>
          <el-input v-model="form.login" placeholder="Логін" size="large">
            <template #prefix>
              <el-icon size="large"><User /></el-icon>
            </template>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-input 
            v-model="form.password" 
            placeholder="Пароль"
            size="large" 
            show-password
          >
            <template #prefix>
              <el-icon size="large"><Lock /></el-icon>
            </template>
          </el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="onSubmit" style="margin-left: auto;">Вхід</el-button>
        </el-form-item>
        <el-form-item>
          <el-button 
            @click="changeIsLogin"
            size="large"
            style="margin: 0 auto;" 
            text
          >
            Змінити пароль
          </el-button>
        </el-form-item>
      </el-form>
</template>

<script>
// import { mapActions } from 'vuex'; 

export default {
  data() {
    return {
      form: {
        login: '',
        password: '',
      }
    };
  },
  methods: {
    async onSubmit() {
      const success = await this.$store.dispatch('login', {
        login: this.form.login,
        password: this.form.password
      });
      
      if (success) {
        console.log("Успішний вхід");
        this.$router.replace({ name: 'Home' });
      } else {
        console.log("Невірний логін або пароль");
      }
    },
    changeIsLogin() {
      this.$emit('change-form');
    },
  },
}
</script>

<style scoped>
.box-card {
  padding: 10px 30px;
  width: 450px;
  border-radius: 15px;
}
.atification_title{
  text-align: center;
  margin-bottom: 40px;
}
.atification_actions{
  justify-content: flex-end;
}
</style>