<template>
  <div class="app common-layout">
    <el-container>
        <el-header class="header" v-if="screenWidth > 768 ">
          <Header @toggle-sidebar="toggleCollapse"/>
        </el-header>
        
        <el-header class="header_mobile" v-if="screenWidth <= 768">
          <HeaderMobile @toggle-sidebar="toggleCollapse"/>
        </el-header>  
      <el-container>
        <Aside v-if="screenWidth > 768 && isLoggedIn" v-model:isCollapse="isAsideCollapsed"/>
        
        <el-main class="main_hight">
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import Header from './components/baseComponents/Header.vue'
import Aside from './components/baseComponents/Aside.vue'
import HeaderMobile from './components/baseComponents/HeaderMobile.vue'

import { ref, onMounted, onBeforeUnmount } from 'vue';

export default {
  name: 'App',
  components: {
    Header,
    Aside,
    HeaderMobile,
  },
  data() {
    return {
      isAsideCollapsed: true
    }
  },
  computed: {
    isLoggedIn() {
      return this.$store.getters.isLoggedIn;
    }
  },
  setup() {
    const screenWidth = ref(window.innerWidth);

    onMounted(() => {
      window.addEventListener('resize', updateScreenWidth);
      setMainHeight();
      window.addEventListener('resize', setMainHeight);
    });

    onBeforeUnmount(() => {
      window.removeEventListener('resize', updateScreenWidth);
      window.removeEventListener('resize', setMainHeight);
    });

    const updateScreenWidth = () => {
      screenWidth.value = window.innerWidth;
    };

    const setMainHeight = () => {
      const headerElement = screenWidth.value > 768 ? 
        document.querySelector('.header') : 
        document.querySelector('.header_mobile');
      const headerHeight = headerElement ? headerElement.offsetHeight : 80; // за замовчуванням
      const viewportHeight = window.innerHeight;
      document.querySelector('.main_hight').style.height = `${viewportHeight - (headerHeight + 16)}px`;
    };

    return {
      setMainHeight, // додавання цієї функції до return об'єкта
      screenWidth,
    };
  },
  methods: {
    toggleCollapse() {
      window.requestAnimationFrame(() => {
        this.isAsideCollapsed = !this.isAsideCollapsed;
        this.setMainHeight(); // оновлюємо висоту після зміни стану
      });
    }
  },
}
</script>


<style>
  * {
    font-family: 'Ubuntu', sans-serif;
  }
  .header, .header_mobile{
    display: flex;
    align-items: center;
    min-height: 80px;
    border-bottom: 1px solid #EEF0F3;
  }
  .header_mobile{
    min-height: 60px;
  }
</style>
