<template>
  <el-form 
        :model="login" 
      >
        <h2 class="atification_title">Зміна паролю</h2>
        <el-form-item>
          <el-input v-model="form.login" placeholder="Логін" size="large">
            <template #prefix>
              <el-icon size="large"><User /></el-icon>
            </template>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-input 
            v-model="form.password" 
            placeholder="Пароль"
            size="large" 
            show-password
          >
            <template #prefix>
              <el-icon size="large"><Lock /></el-icon>
            </template>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-input 
            v-model="form.newPassword" 
