          <el-button type="primary" @click="onSubmit">Вхід</el-button>
        </el-form-item>
      </el-form>
</template>

<script>
export default {
  data() {
    return {
      form: {
        login: '',
        password: '',
        newPassword: '',
        confirmNewPassword: '',
      }
    };
  },
  methods: {
    onSubmit() {
      console.log('submit!')
    },
    changeIsLogin() {
      // Відсилаємо подію в батьківський компонент
      this.$emit('change-form');
    },
  },
}
</script>

