<template>
  <div class="header_mobile-container">
    <el-button 
      @click="aside = true"
      :disabled="!isLoggedIn"
      type="primary" 
      size="large" 
      plain 
      circle
    >
      <el-icon size="large"><Menu/></el-icon>
    </el-button>
    <img alt="Antonov logo" src="../../assets/icons/an_logo.svg">
    <el-button 
      v-if="isLoggedIn"
      @click="moreAction = true"
      type="primary" 
      size="large" 
      plain 
      circle
    >
    <el-icon size="large"><MoreFilled /></el-icon>
    </el-button>
    <el-button 
      v-else
      @click="langSwitch = true"
      type="primary" 
      size="large" 
      plain 
      circle
    >
    <el-icon size="large"><Guide /></el-icon>
    </el-button>
  </div>
  
  <el-drawer 
    v-model="aside" 
    :with-header="false"
    direction="ltr"
    size="80%"
  >
    <AsideMobile @close-drawer="aside = false"/>
  </el-drawer>

  <el-drawer 
    v-model="moreAction" 
    :with-header="false"
    direction="btt"
    size="40%"
  >
    <el-menu
      class="more_action-menu"
      @open="handleOpen"
      @close="handleClose"
    >
      <el-menu-item 
        index="1"
        class="more_action-item"
        @click="closeMoreAction"
        disabled
      >
        <el-icon><Guide /></el-icon>
        <span>Українська</span>
      </el-menu-item>
      <el-divider class="more_action-divider"/>
      <el-menu-item 
        index="2"
        class="more_action-item"
        @click="closeMoreAction"
      >
        <el-icon><UserFilled /></el-icon>
        <span>Мій профіль</span>
      </el-menu-item>
      <el-divider class="more_action-divider"/>
      <el-menu-item 
        index="3"
        class="more_action-item"
        @click="closeMoreAction"
      >
        <el-icon><Message/></el-icon>
        <span>Повідомлення</span>
      </el-menu-item>
      <el-divider class="more_action-divider"/>
      <el-menu-item 
        index="4"
        class="more_action-item"
        @click="logout"
      >
        <el-icon><SwitchButton /></el-icon>
        <span>Вийти</span>
      </el-menu-item>
    </el-menu>
  </el-drawer>
  <el-drawer 
    v-model="langSwitch" 
    :with-header="false"
    direction="btt"
    size="15%"
  >
    <el-menu
      class="more_action-menu"
      @open="handleOpen"
      @close="handleClose"
    >
      <el-menu-item 
        index="1"
        class="more_action-item"
        @click="closeMoreAction"
        disabled
      >
        <el-icon><Guide /></el-icon>
        <span>Українська</span>
      </el-menu-item>
    </el-menu>
  </el-drawer>
</template>

<script>
import AsideMobile from './AsideMobile.vue'
export default {
  name: 'HeaderMobile',
  components: {
    AsideMobile,
  },
  data() {
    return {
      aside: false,
      moreAction: false,
      langSwitch: false
    };
  },
  computed: {
    isLoggedIn() {
      return this.$store.getters.isLoggedIn;
    }
  },
  methods:{
    closeMoreAction() {
        this.moreAction = false;
    },
    logout() {
      this.$store.commit('setLogin', false);
      this.$router.push('/login');
      this.closeMoreAction()
    }
  }
};
</script>

<style>
  .header_mobile-container{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .more_action-menu{
    border-right: 0;
  }
  .more_action-item{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .more_action-divider{
    margin: 0 0;
  }
</style>