<template>
  <aside :class="{ 'is-collapsed': isCollapse }">
    <el-menu
      default-active="2"
      class="el-menu-vertical"
      :collapse="isCollapse"
      @open="handleOpen"
      @close="handleClose"
    >
      <el-sub-menu index="1">
        <template #title>
          <el-icon><location /></el-icon>
          <span>Navigator One</span>
        </template>
        <el-menu-item-group>
          <template #title><span>Group One</span></template>
          <el-menu-item index="1-1">item one</el-menu-item>
          <el-menu-item index="1-2">item two</el-menu-item>
        </el-menu-item-group>
        <el-menu-item-group title="Group Two">
          <el-menu-item index="1-3">item three</el-menu-item>
        </el-menu-item-group>
        <el-sub-menu index="1-4">
          <template #title><span>item four</span></template>
          <el-menu-item index="1-4-1">item one</el-menu-item>
        </el-sub-menu>
      </el-sub-menu>
      <el-menu-item index="2" @click="navigate('/')">
        <el-icon><icon-menu /></el-icon>
        <template #title>Home</template>
      </el-menu-item>
      <el-menu-item index="3" @click="navigate('/profile')">
        <el-icon><document /></el-icon>
        <template #title>My Profile</template>
      </el-menu-item>
      <el-menu-item index="4">
        <el-icon><setting /></el-icon>
        <template #title>Navigator Four</template>
      </el-menu-item>
    </el-menu>
  </aside>
</template>

<script>
import { ref, watch } from 'vue';
import { useRouter } from 'vue-router';
import {
  Document,
  Menu as IconMenu,
  Location,
  Setting,
} from '@element-plus/icons-vue';

export default {
  name: 'Sidebar',
  
  props: {
    isCollapse: {
      type: Boolean,
      default: true
    }
  },
  
  components: {
    Document,
    IconMenu,
    Location,
    Setting
  },

  setup(props, { emit }) {
    const router = useRouter();
    const navigate = (path) => {
      router.push(path);
    };

    const localIsCollapse = ref(props.isCollapse);

    // Спостерігати за змінами у локальному стані та відправляти їх батьківському компоненту
    watch(localIsCollapse, (newVal) => {
      emit('update:isCollapse', newVal);
    });

    const handleOpen = (key, keyPath) => {
      console.log(key, keyPath);
    };

    const handleClose = (key, keyPath) => {
      console.log(key, keyPath);
    };

    return {
      handleOpen,
      handleClose,
      navigate
    };
  }
};
</script>

<style>
  .el-aside {
    width: 200px;
    transition: width 0.5s ease;
    overflow: hidden;
  }
  .el-aside.is-collapsed {
    width: 80px;
  }
  .el-menu-vertical{
    height: calc(100% + 8px);
  }
  .el-menu-vertical:not(.el-menu--collapse) {
    width: 100%; /* Змінено, щоб ширина меню завжди була повною шириною aside */
    min-height: 100%;
  }
  .router-link-active {
    color: inherit;
    text-decoration: none;
  }
  
  .router-link-exact-active {
    color: inherit;
    text-decoration: none;
  }
  .full-width-link {
    display: flex;
    align-items: center;
    width: 100%;
  }
  
  .el-aside.is-collapsed .full-width-link span {
    display: none;
  }
  
</style>